body {
  background-color: black;
  color: white;
  margin: 0;
  font-family: "GmarketSansMedium";
}

a {
  text-decoration: none;
  color: white;
  cursor: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
ul,
ol {
  margin: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}
